<template>
  <div class="login df aic jcc">
    <el-form :model="form" hide-required-asterisk class="plr40 br10 login-form">
      <h3>学生管理后台系统</h3>
      <el-form-item>
        <el-input
          autofocus
          maxlength="16"
          clearable
          v-model="form.uid"
          type="text"
          prefix-icon="el-icon-user-solid"
          placeholder="请输入账号"
          @keyup.enter.native="handleLogin"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-input
          maxlength="16"
          minlength="6"
          v-model="form.password"
          prefix-icon="el-icon-lock"
          placeholder="请输入密码"
          show-password
          @keyup.enter.native="handleLogin"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-checkbox v-model="form.checked">记住我</el-checkbox>
      </el-form-item>
      <el-form-item>
        <el-button
          class="df aic jcc login-button"
          type="primary"
          @click.native.prevent="handleLogin"
        >
          <span>登录</span>
        </el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import Cookies from "js-cookie";
export default {
  name: "login",
  data() {
    return {
      form: {
        uid: "",
        password: "",
        checked: false //控制登录的时候是否记住我，只有登录成功了才记住我
      }
    };
  },
  methods: {
    handleLogin() {
      const loading = this.$loading({
        lock: true,
        text: "拼命加载中...",
        spinner: "el-icon-loading",
        background: "rgba(0, 0, 0, 0.8)"
      });

      this.$post("user/login", {
        uid: this.form.uid,
        password: this.form.password
      })
        .then(res => {
          if (!res.uid) {
            this.$notify({
              title: "提示",
              message: res,
              type: "warning"
            });
            loading.close();
            return;
          }
          if (this.form.checked) {
            Cookies.set("uid", this.form.uid, { expires: 30 });
            Cookies.set("password", this.form.password, { expires: 30 });
            Cookies.set("checked", this.form.checked, { expires: 30 });
          } else {
            Cookies.remove("uid");
            Cookies.remove("password");
            Cookies.remove("checked");
          }
          loading.close();
          this.$router.replace("/home");
        })
        .catch(res => {
          loading.close();
        });
    }
  }
};
</script>

<style>
.login {
  background: url(../../assets/img/login.jpg) no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
}
.login-form {
  background: #fff;
  width: 300px;
}
.login-form > h3 {
  text-align: center;
}
.login-button {
  width: 100%;
}
</style>


